import React from 'react';

import fbSvg from '../../assets/icons/fb.svg';
import googleSvg from '../../assets/icons/google.svg';

function ExternalLogin() {
    return (
        <div className="d-flex justify-content-between w-100">
            <div className="auth__modal__with__fb d-flex align-items-center" >
                <div style={{marginRight: '10px'}}>
                    <img src={fbSvg} alt="fb" />
                </div>

                <div>
                    <span>ავტორიზაცია Facebook-ით</span>
                </div>
            </div>

            <div className="auth__modal__with__google d-flex align-items-center" >
                <div style={{marginRight: '10px'}}>
                    <img src={googleSvg} alt="google" />
                </div>

                <div>
                    <span>ავტორიზაცია Google-ით</span>
                </div>
            </div>
        </div>
    )
}

export default ExternalLogin;
