import React from 'react';
import { Link } from 'react-router-dom';

import closeSvg from '../../assets/icons/close.svg';

function MobileCategories(props) {
    const menuCategoriesList = [
        {
            urlName: 'მთავარი',
            urlPath: '/'
        },
        {
            urlName: 'ანიმაციური',
            urlPath: '/movies'
        },
        {
            urlName: 'დრამა',
            urlPath: '/'
        },
        {
            urlName: 'კომედია',
            urlPath: '/'
        },
        {
            urlName: 'მისტიკა',
            urlPath: '/'
        },
        {
            urlName: 'ბიოგრაფიული',
            urlPath: '/'
        },
        {
            urlName: 'ეროტიული',
            urlPath: '/'
        },
        {
            urlName: 'მელოდრამა',
            urlPath: '/'
        },
        {
            urlName: 'მუსიკალური',
            urlPath: '/'
        },
        {
            urlName: 'საომარი',
            urlPath: '/'
        },
        {
            urlName: 'მელოდრამა',
            urlPath: '/'
        },
        {
            urlName: 'მუსიკალური',
            urlPath: '/'
        },
        {
            urlName: 'საომარი',
            urlPath: '/'
        }
    ]

    return (
        <div className="d-flex flex-column text-center h-100 mobile__nav__categories__wrapper">
            {
                menuCategoriesList.map((el, ind) => {
                    return <div key={ind} className="w-100 mobile__nav__categories__link" style={{ animationDelay: `${ind / 10}s` }}>
                        <Link to={el.urlPath} className="text-decoration-none" onClick={props.closeCategories}>
                            {el.urlName}
                        </Link>
                    </div>
                })
            }
            <div className="mobile__nav__categories__link">
                <br />
                <br />
            </div>
            <div className="mobile__nav__categories__close" onClick={props.closeCategories}>
                <img src={closeSvg} alt="close_nav" />
            </div>
        </div>
    )
}

export default MobileCategories;
