import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Navigation } from 'swiper';
import useFetch from '../../custom-hooks/useFetch';
import { useEffect, useState } from 'react';

import 'swiper/swiper-bundle.css';
import { Link, useLocation } from 'react-router-dom';
import OptGroup from './OptGroup';

SwiperCore.use([Navigation]);

function Row({ title, className, fetchUrl, links, seeMoreUrl, forModalProps }) {
    const [movies, setmovies] = useState([]);
    const { data, isPending, error } = useFetch(fetchUrl);
    const [slideWidth, setSlideWidth] = useState(0);
    const [slideHeight, setSlideHeight] = useState(0);

    const filterCategory = (e) => {
        if (e !== undefined) {
            const filteredMovie = data.filter(movie => movie.category === e);
            setmovies(filteredMovie);
        }
    }

    const toggleActive = (e) => {
        document.querySelectorAll('.home__url ').forEach(elem => {
            elem.classList.remove('active');
        });

        e.classList.add('active');
    }

    const showPrevArrow = (e) => {
        let prevArrow = e.el.querySelectorAll('.swiper-button-prev');
        prevArrow.forEach(arrow => arrow.setAttribute('style', 'display: flex !important'));
    }

    const handleSlideScaling = (e) => {
        e.closest('.home__banner').classList.add("scaled");
        // document.querySelector('body').classList.add('modal__preview');
    }

    useEffect(() => {
        setmovies(data);
    }, [data])

    const location = useLocation();

    return (
        <div className={`home__${className} home__category`}>
            {error && <div>{error}</div>}

            {isPending && <div>Loading...</div>}

            {
                movies &&

                <div>
                    <div className="home__category__header">
                        <div className="d-flex align-items-center justify-content-between">
                            <div className="d-flex align-items-center">
                                <div className="d-flex align-items-center home__category__heading">
                                    <h2>{title}</h2>
                                </div>

                                <div className="d-flex align-items-center">
                                    {
                                        links?.map((el, id) => {
                                            return <div
                                                key={id}
                                                onClick={() => filterCategory(el.category)}
                                                className="home__category__filter__link"
                                            >
                                                <div
                                                    className={`home__url ` + (id === 0 ? 'active' : '')}
                                                    onClick={(e) => { toggleActive(e.target) }}
                                                >
                                                    {el.linkTitle}
                                                </div>
                                            </div>
                                        })
                                    }
                                </div>
                            </div>

                            <div>
                                <a href={seeMoreUrl} className="home__url home__url--see__more">ნახე ყველა</a>
                            </div>
                        </div>
                    </div>

                    <div className="home__category__header home__category__header--mobile">
                        <div className="d-flex align-items-center justify-content-between">
                            <div className="d-flex flex-column">
                                <div className="d-flex align-items-center home__category__heading">
                                    <a href={seeMoreUrl} className="text-decoration-none text-white">
                                        <h2>{title}</h2>
                                    </a>
                                </div>

                                <div className="d-flex align-items-center">
                                    {
                                        links?.map((el, id) => {
                                            return <div
                                                key={id}
                                                onClick={() => filterCategory(el.category)}
                                                className="home__category__filter__link"
                                            >
                                                <div
                                                    className={`home__url ` + (id === 0 ? 'active' : '')}
                                                    onClick={(e) => { toggleActive(e.target) }}
                                                >
                                                    {el.linkTitle}
                                                </div>
                                            </div>
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>

                    <Swiper
                        slidesPerGroup={7}
                        spaceBetween={5}
                        allowTouchMove={false}
                        loop={true}
                        speed={1700}
                        navigation
                        className="home__swiper"
                        onSlideChange={showPrevArrow}
                        breakpoints={
                            {
                                1400: {
                                    slidesPerView: 7.3
                                },
                                1250: {
                                    slidesPerView: 6.3
                                },
                                1090: {
                                    slidesPerView: 5.3
                                },
                                798: {
                                    slidesPerView: 5.3
                                },
                                0: {
                                    slidesPerView: 3.3
                                }
                            }
                        }
                    >
                        {movies?.map((el, ind) => {
                            return <SwiperSlide
                                key={ind}
                                className={`home__banner`}
                                onClick={e => handleSlideScaling(e.target)}
                            >
                                <Link to={{
                                    pathname: location.pathname,
                                    search: `preview=true&category=${el.urlCategory}&id=${el.id}`
                                }}
                                    className="w-100"
                                >
                                    <img src={el.imgSrc} alt={el.title} className="main-border-radius w-100" />
                                </Link>

                                <OptGroup
                                    likePercentage={el.likePercentage || ''}
                                    duration={el.duration || ''}
                                    geoTitle={el.geoTitle || ''}
                                    engTitle={el.engTitle || ''}
                                    isGeorgian={el.isGeorgian || ''}
                                    isEnglish={el.isEnglish || ''}
                                    isRussian={el.isRussian || ''}
                                    elTitle={el.urlCategory}
                                    elID={el.id}
                                />
                            </SwiperSlide>
                        })}
                    </Swiper>
                </div>
            }
        </div >
    )
}

export default Row
