import React, { useEffect, useState } from 'react';

import userSvg from '../../assets/icons/user_default.svg';
import replyArrowSvg from '../../assets/icons/reply_arrow.svg';
import likeBtnSvg from '../../assets/icons/like.svg';

function Comments({ commentsList, postComment }) {
    const [isLiked, setIsLiked] = useState(false);
    const [likeAmount, setLikeAmount] = useState(0);
    const [getComment, setGetPostComment] = useState(false);
    const [comments, setComments] = useState([]);

    const showReplyField = e => {
        const parent = e.target.closest('.comments__section__comment__group, .comments__section__reply__group');
        const tag = parent.querySelector('.comments__section__comment__author').textContent;
        const field = parent.querySelector('.comments__field__input');

        parent.querySelector('.comments__section__reply__field').classList.toggle('active');
        field.innerHTML = '<span class="reply_tag" contenteditable="false">@' + tag + '</span>';
        field.focus();
    }

    useEffect(() => {
        setComments(commentsList);
    }, [commentsList]);

    const handleKeyDown = e => {
        let inputField = e.target;
        let height = e.target.getBoundingClientRect().height;

        if (inputField.textContent.length >= 250 && e.code.toLocaleLowerCase() !== 'backspace' && height > 150) {
            e.preventDefault();
            return false;
        }

        if (height > 150 && e.key.toLocaleLowerCase() === 'enter') {
            e.preventDefault();
            return false;
        }
    }

    const handlePaste = e => {
        var clipboardData, pastedData;
        e.stopPropagation();
        e.preventDefault();

        clipboardData = e.clipboardData || window.clipboardData;
        pastedData = clipboardData.getData('Text');
        e.target.innerText += pastedData.substring(0, 250);
    }

    const like = e => {
        let likeCount = parseInt(e.target.parentElement.querySelector('.comments__section__like__btn__like__count').textContent);

        // setIsLiked(!isLiked);
        // setLikeAmount(likeCount);

        // if (isLiked) {
        //     setLikeAmount(likeAmount + 1);
        // } else {
        //     likeCount = parseInt(likeAmount);
        // }
        // e.target.parentElement.querySelector('.comments__section__like__btn__like__count').innerText = likeCount

        // console.log(likeCount);
    }

    return (
        <div className="comments">
            <div className="comments__heading">
                <h3>კომენტარები</h3>
            </div>

            <div className="comments__field d-flex">
                <div className="comments__field__avatar">
                    <img src={userSvg} alt="user" />
                </div>

                <div className="d-flex flex-column align-items-end comments__field__input__group fieldgroup" data-category="comment">
                    <div className="comments__field__input w-100"
                        contentEditable="true"
                        onPaste={e => handlePaste(e)}
                        onKeyDown={e => handleKeyDown(e)}
                    ></div>

                    <div className="comments__field__submit" onClick={(e) => {
                        let inputField = e.target.closest('.comments__field__input__group').querySelector('.comments__field__input');

                        inputField.textContent.replace(/\s/g, '').length > 0 && postComment(e.target.closest('.comments__field__input__group').querySelector('.comments__field__input'));
                        setGetPostComment(!getComment);
                    }}
                    >
                        <span>გამოქვეყნება</span>
                    </div>
                </div>
            </div>

            <div>
                {
                    comments && comments.slice(0).reverse().map((comment, commentIndex) => {
                        // comment

                        return <div className="comments__section" key={comment.id}>

                            <div className="comments__section__comment fieldgroup" data-category="reply">
                                <div className="d-flex align-items-start">
                                    <div className="comments__field__avatar">
                                        <img src={comment.profilePicSrc} alt="user" />
                                    </div>

                                    <div className="comments__section__comment__group">
                                        <div className="comments__section__comment__info">
                                            <div className="d-flex align-items-center">
                                                <div className="comments__section__comment__author">
                                                    {comment.author}
                                                </div>

                                                <div className="comments__section__comment__date">
                                                    {comment.dateCreated}
                                                </div>
                                            </div>
                                        </div>

                                        <div>
                                            <div
                                                className="comments__section__comment__body"
                                                style={{ whiteSpace: 'pre-wrap', wordBreak: 'break-word' }}
                                                dangerouslySetInnerHTML={{ __html: comment.comment.replaceAll(/^\s+|\s+$/g, '') }}
                                            ></div>

                                            <div className="d-flex align-items-center">
                                                <div className="comments__section__reply__btn d-flex align-items-center" onClick={e => showReplyField(e)}>
                                                    <img src={replyArrowSvg} alt="reply" />
                                                    <span>უპასუხე</span>
                                                </div>

                                                <div className="comments__section__like__btn d-flex align-items-center">
                                                    <img src={likeBtnSvg} alt="like" onClick={e => like(e)} />
                                                    <div className="comments__section__like__btn__like__count">{likeAmount}</div>
                                                    <span>მოწონება</span>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="comments__section__reply__field">
                                            <div className="d-flex align-items-start">
                                                <div className="comments__field__avatar">
                                                    <img src={userSvg} alt="user" />
                                                </div>

                                                <div className={`d-flex flex-column align-items-end comments__field__input__group`}>
                                                    <div className="comments__field__input w-100"
                                                        contentEditable="true"
                                                        onPaste={e => handlePaste(e)}
                                                        onKeyDown={e => handleKeyDown(e)}
                                                    ></div>
                                                    <div className="comments__field__submit comments__field__submit--for__reply" onClick={(e) => {
                                                        let inputField = e.target.closest('.comments__field__input__group').querySelector('.comments__field__input');

                                                        inputField.textContent.replace(/\s/g, '').length > 0 && postComment(e.target.closest('.comments__field__input__group').querySelector('.comments__field__input'));
                                                        setGetPostComment(!getComment);
                                                    }}>
                                                        <span>გამოქვეყნება</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* Reply */}

                            <div className={`comments__section__reply fieldgroup ${!comment?.replies ? 'd-none' : ''}`} data-category="reply">
                                {
                                    comment?.replies && comment.replies.map(reply => {
                                        return <div key={reply.id}>
                                            <div className="d-flex align-items-start">
                                                <div className="comments__field__avatar">
                                                    <img src={reply.profilePicSrc} alt="user" />
                                                </div>

                                                <div className="comments__section__reply__group">
                                                    <div className="comments__section__comment__info">
                                                        <div className="d-flex align-items-center">
                                                            <div className="comments__section__comment__author">
                                                                {reply.author}
                                                            </div>

                                                            <div className="comments__section__comment__date">
                                                                {reply.dateCreated}
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div>
                                                        <div className="comments__section__comment__body">
                                                            {reply.reply}
                                                        </div>

                                                        <div className="d-flex align-items-center">
                                                            <div className="comments__section__reply__btn d-flex align-items-center" onClick={e => showReplyField(e)}>
                                                                <img src={replyArrowSvg} alt="reply" />
                                                                <span>უპასუხე</span>
                                                            </div>

                                                            <div className="comments__section__like__btn d-flex align-items-center">
                                                                <img src={likeBtnSvg} alt="like" onClick={e => like(e)} />
                                                                <div className="comments__section__like__btn__like__count">{likeAmount}</div>
                                                                <span>მოწონება</span>
                                                            </div>
                                                        </div>
                                                    </div>

                                                    <div className="comments__section__reply__field">
                                                        <div className="d-flex align-items-start">
                                                            <div className="comments__field__avatar">
                                                                <img src={userSvg} alt="user" />
                                                            </div>

                                                            <div className={`d-flex flex-column align-items-end reply__field__input__group`}>
                                                                <div className="comments__field__input w-100"
                                                                    contentEditable="true"
                                                                    onPaste={e => handlePaste(e)}
                                                                    onKeyDown={e => handleKeyDown(e)}></div>

                                                                <div className="comments__field__submit comments__field__submit--for__reply" onClick={(e) => {
                                                                    let inputField = e.target.closest('.reply__field__input__group').querySelector('.comments__field__input');

                                                                    inputField.textContent.replace(/\s/g, '').length > 0 && postComment(e.target.closest('.reply__field__input__group').querySelector('.comments__field__input'));
                                                                    setGetPostComment(!getComment);
                                                                }}>
                                                                    <span>გამოქვეყნება</span>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    })
                                }
                            </div>

                        </div>
                    })
                }
            </div>
        </div>
    )
}

export default Comments;
