import React, { useState } from 'react';
import useFetch from '../../custom-hooks/useFetch';

import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Navigation } from 'swiper';
import FilterHeaderMovies from './FilterHeaderMovies';

import closeBtn from '../../../src/assets/icons/close.svg';

import fetchLinks from '../../FetchLinks';
import { useLocation } from 'react-router';
import { Link } from 'react-router-dom'
import FilterHeaderMoviesMobile from './FilterHeaderMoviesMobile';


SwiperCore.use([Navigation]);

function Movies() {

    const { data, isPending, error } = useFetch(fetchLinks.moviesFetchLink);

    const [filteredValues, setFilteredValues] = useState([]);
    const [uncheckValue, setUncheckValue] = useState([]);

    const getFilteredValues = values => {
        setFilteredValues(values)
    }

    const handleTagRemove = (e) => {
        setUncheckValue(e.target.closest('.filter__tag').querySelector('.filter__tag__name').innerText);
    }

    const filterBy = (e) => {
        document.querySelectorAll('.movies__filter__element').forEach(elem => {
            elem.classList.remove('active');
        });
        e.classList.add('active');
    }

    const location = useLocation();

    var settings = {
        allowTouchMove: false,
        loop: false,
        navigation: true,
        className: 'movies__slider',
        allowTouchMove: true,
        breakpoints: {
            1600: {
                spaceBetween: 10,
                slidesPerView: 6.3
            },
            1400: {
                spaceBetween: 10,
                slidesPerView: 5.3
            },
            1250: {
                spaceBetween: 10,
                slidesPerView: 5.3
            },
            1090: {
                spaceBetween: 10,
                slidesPerView: 4.5
            },
            798: {
                spaceBetween: 5,
                slidesPerView: 4.5
            },
            0: {
                spaceBetween: 5,
                slidesPerView: 3.4
            }
        }
    }

    return (
        <div className="container-fluid p-0 main__container">
            {error && <div>{error}</div>}

            {isPending && <div>Loading...</div>}

            <div className={`row movies`}>
                <div className={`d-flex flex-column movies__left-side col-xl-3 col-lg-4 col-md-12`}>
                    <div>
                        <h1>რეიტინგული ფილმები</h1>
                    </div>

                    <div className={`movies__filter`}>
                        <div className="row">
                            <div className={`movies__filter__element active`} onClick={e => filterBy(e.target)}>
                                <span>დღის</span>
                            </div>
                            <div className={`movies__filter__element`} onClick={e => filterBy(e.target)}>
                                <span>კვირის</span>
                            </div>
                            <div className={`movies__filter__element`} onClick={e => filterBy(e.target)}>
                                <span>თვის</span>
                            </div>
                            <div class="col-xl-12 col-lg-12 d-xl-block d-lg-block d-none">
                                <div className={`movies__filter__element`} onClick={e => filterBy(e.target)}>
                                    <span>ყველა</span>
                                </div>
                            </div>

                            <div class="d-xl-none d-lg-none d-block">
                                <div className={`movies__filter__element`} onClick={e => filterBy(e.target)}>
                                    <span>ყველა</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-xl-9 col-lg-8 col-md-12" onMouseDown={e => console.log(123)}>
                    <Swiper {...settings} onTouchStart={(e)=> e.el.style.cursor = "grabbing"} onTouchEnd={(e)=> e.el.style.cursor = "grab"}>
                        {
                            data.map((el, ind) => {
                            return <SwiperSlide
                                key={ind}
                                className={`movies__slider__slide`}
                                style={{ backgroundImage: `url(${el.imgSrc})` }} 
                                >

                            </SwiperSlide>
                        })
                        }
                    </Swiper>
                </div>
            </div>

            {/* filter header desktop */}
            <div class="filter__header__desktop">
                <FilterHeaderMovies data={data} getFilteredValues={getFilteredValues} uncheckValue={uncheckValue} />
            </div>
            {/* filter header desktop */}

            {/* filter header mobile */}
            <div class="filter__header__mobile">
                <FilterHeaderMoviesMobile data={data} getFilteredValues={getFilteredValues} uncheckValue={uncheckValue} />
            </div>
            {/* filter header mobile */}

            <div style={{ margin: '0 60px' }}>
                {
                    filteredValues?.map((el, ind) => {
                        return <div key={ind} className="d-flex flex-wrap flex-row-reverse justify-content-end">

                            {
                                el.genre?.map((subEl, subInd) => <div className="filter__tag" key={subInd}>
                                    <span className="filter__tag__name">{subEl}</span>
                                    <div className="filter__tag__close" onClick={handleTagRemove}><img src={closeBtn} /></div>
                                </div>)
                            }


                            {
                                el.dub?.map((subEl, subInd) => <div className="filter__tag" key={subInd}>
                                    <span className="filter__tag__name">{subEl}</span>
                                    <div className="filter__tag__close" onClick={handleTagRemove}><img src={closeBtn} /></div>
                                </div>)
                            }


                            {
                                el.country?.map((subEl, subInd) => <div className="filter__tag" key={subInd}>
                                    <span className="filter__tag__name">{subEl}</span>
                                    <div className="filter__tag__close" onClick={handleTagRemove}><img src={closeBtn} /></div>
                                </div>)
                            }


                            {
                                el.IMDBrating && <div className="filter__tag">
                                    <span className="filter__tag__name mr-1">{el.IMDBrating}</span> <span> და მეტი</span>
                                    <div className="filter__tag__close" onClick={handleTagRemove}><img src={closeBtn} /></div>
                                </div>
                            }


                            {
                                el.ourRating && <div className="filter__tag">
                                    <span className="filter__tag__name mr-1">{el.ourRating}</span> <span> და მეტი</span>
                                    <div className="filter__tag__close" onClick={handleTagRemove}><img src={closeBtn} /></div>
                                </div>
                            }
                        </div>
                    })
                }
            </div>

            <div className="content">
                {
                    data.map((el, ind) => {
                        return <div key={ind} className="content__items">
                            <Link to={{
                                pathname: location.pathname,
                                search: `preview=true&category=${el.urlCategory}&id=${el.id}`
                            }}>
                                <img src={el.imgSrc} alt="" className="w-100" />
                            </Link>
                        </div>
                    })
                }
            </div>
        </div>
    )
}

export default Movies
