import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import useFetch from '../../custom-hooks/useFetch';
import { useEffect, useState } from 'react';

import Slider from "react-slick";

// import "~slick-carousel/slick/slick.css"; 
// import "~slick-carousel/slick/slick-theme.css";

import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
import { Link } from 'react-router-dom';

function TwoRow({ title, className, fetchUrl, links, seeMoreUrl }) {
    const { data, isPending, error } = useFetch(fetchUrl);
    const [movies, setmovies] = useState([]);

    const filterCategory = (e) => {
        if (e !== undefined) {
            const filteredMovie = data.filter(movie => movie.category === e);
            setmovies(filteredMovie);
        }
    }

    const toggleActive = e => {
        e.preventDefault();
    }

    const showPrevArrow = (e) => {
        let prevArrow = e.el.querySelectorAll('.swiper-button-prev');
        prevArrow.forEach(arrow => arrow.setAttribute('style', 'display: flex !important'));
    }

    useEffect(() => {
        setmovies(data);
    }, [data]);

    const sliderSettings = {
        infinite: true,
        loop: true,
        slidesToShow: 3.625,
        speed: 500,
        rows: 2,
        slidesPerRow: 2,
        arrows: false,
        draggable: true,
        swipeToSlide: true,
        swipe: true,
        autoplay: true,
        autoplaySpeed: 5000
    };

    document.querySelectorAll('.home__banner__newly__added__series__title').forEach(elem => {
        elem.remove();
    })

    return (
        <div className={`home__${className} home__category`}>
            {error && <div>{error}</div>}
            {isPending && <div>Loading...</div>}

            {
                movies &&

                <div>
                    <div className="home__category__header">
                        <div className="d-flex align-items-center justify-content-between">
                            <div className="d-flex align-items-center">
                                <div className="d-flex align-items-center home__category__heading">
                                    <h2>{title}</h2>
                                </div>

                                <div className="d-flex align-items-center">
                                    {
                                        links?.map((el, id) => {
                                            return <div
                                                key={id}
                                                onClick={() => filterCategory(el.category)}
                                                className="home__category__filter__link"
                                            >
                                                <a
                                                    href={el.linkUrl}
                                                    className={`home__url ` + (id === 0 ? 'active' : '')}
                                                    onClick={toggleActive}
                                                >
                                                    {el.linkTitle}
                                                </a>
                                            </div>
                                        })
                                    }
                                </div>
                            </div>

                            <div>
                                <Link to={seeMoreUrl} className="home__url home__url--see__more">ნახე ყველა</Link>
                            </div>
                        </div>
                    </div>

                    <div className="container-fluid p-0">
                        <Swiper
                            slidesPerView={2}
                            slidesPerGroup={2}
                            spaceBetween={5}
                            slidesPerColumnFill='row'
                            allowTouchMove={false}
                            loop={true}
                            speed={1700}
                            navigation
                            allowTouchMove={true}
                            onSlideChange={showPrevArrow}
                            breakpoints={
                                {
                                    1400: {
                                        slidesPerView: 6.3,
                                        slidesPerColumn: 2
                                    },
                                    1250: {
                                        slidesPerView: 5.3,
                                        slidesPerColumn: 2
                                    },
                                    1090: {
                                        slidesPerView: 3,
                                        slidesPerColumn: 3
                                    },
                                    798: {
                                        slidesPerView: 3,
                                        slidesPerColumn: 3
                                    },
                                    0: {
                                        slidesPerView: 3,
                                        slidesPerColumn: 3
                                    }
                                }
                            }
                            style={{paddingLeft: '60px'}}
                        >
                            {movies?.map((el, ind) => {
                                return <SwiperSlide key={ind} className="" style={{ width: '290px', height: '165px' }}>
                                    <a href="#" className="text-decoration-none text-white">
                                        <div className={`home__banner__${className}`} style={{ width: '100%', height: '100%', marginRight: '5px' }}>
                                            <div className={`home__banner__${className}__title`}>
                                                <h3>{el.title}</h3>
                                                <div className={`home__banner__${className}__title__quantity`}>
                                                    <span>{el.quantity} ფილმი</span>
                                                </div>
                                            </div>
                                            <img src={el.imgSrc} alt={el.title} className="main-border-radius w-100 h-100" />
                                        </div>
                                    </a>
                                </SwiperSlide>
                            })}
                        </Swiper>
                    </div>
                </div>
            }

        </div>
    )
}

export default TwoRow
