import React from 'react';
import { Link } from 'react-router-dom';

import footerLogo from '../../assets/images/footer-logo.svg';
import footerLogoHover from '../../assets/images/footer-logo-hover.svg';
import warning18 from '../../assets/images/footer-18-warning.png';
import phoneSvg from '../../assets/icons/phone.svg';
import mailSvg from '../../assets/icons/mail.svg';
import fbSvg from '../../assets/icons/fb.svg';
import instSvg from '../../assets/icons/inst.svg';
import topGe from '../../assets/icons/top-ge.svg';

function Footer() {
    return (
        <footer>
            <div className="container footer">
                {/* desktop version */}

                <div className="d-xl-flex d-lg-flex d-md-flex d-none flex-wrap justify-content-between">
                    <div className="footer__col">
                        <Link to="/home">
                            <div className="position-relative">
                                <div className="footer__logo">
                                    <img src={footerLogo} alt="footer logo" />
                                </div>

                                <div className="footer__logo footer__logo--hover">
                                    <img src={footerLogoHover} alt="footer logo" />
                                </div>
                            </div>
                        </Link>
                    </div>

                    <div className="footer__col">
                        <h4>კომპანია</h4>
                        <div className="footer__links">
                            <div className="footer__links__link">
                                <Link>ფილმის მოთხოვნა</Link>
                            </div>

                            <div className="footer__links__link">
                                <Link>რეკლამა საიტზე</Link>
                            </div>
                        </div>
                    </div>

                    <div className="footer__col">
                        <h4>კატეგორიები</h4>
                        <div className="footer__links">
                            <div className="footer__links__link">
                                <Link to="/movies">ფილმები</Link>
                            </div>

                            <div className="footer__links__link">
                                <Link to="/series">სერიალები</Link>
                            </div>

                            <div className="footer__links__link">
                                <Link to="/my-list">ჩემი სიები</Link>
                            </div>

                            <div className="footer__links__link">
                                <Link to="/news">სიახლეები</Link>
                            </div>
                        </div>
                    </div>

                    <div className="footer__col">
                        <h4>პროფილი</h4>
                        <div className="footer__links">
                            <div className="footer__links__link">
                                <Link>ჩემი ანგარიში</Link>
                            </div>

                            <div className="footer__links__link">
                                <Link>ჩემი გვერდი</Link>
                            </div>
                        </div>
                    </div>

                    <div className="footer__col">
                        <h4>დაგვიკავშირდით</h4>
                        <div className="footer__links">
                            <div className="footer__links__link">
                                <a href="tel:+995 123-123-123">
                                    <div className="d-flex align-items-center">
                                        <div className="footer__links__link__icon">
                                            <img src={phoneSvg} alt="phone" />
                                        </div>
                                        <span>123-123-123</span>
                                    </div>
                                </a>
                            </div>

                            <div className="footer__links__link">
                                <a href="mailto:Mail@mail.com">
                                    <div className="d-flex align-items-center">
                                        <div className="footer__links__link__icon">
                                            <img src={mailSvg} alt="mail" />
                                        </div>
                                        <span>Mail@mail.com</span>
                                    </div>
                                </a>
                            </div>

                            <div className="footer__links__link">
                                <a href="https://www.facebook.com/Croconet.Ge/" target="_blank" rel="noopener noreferrer">
                                    <div className="d-flex align-items-center">
                                        <div className="footer__links__link__icon">
                                            <img src={fbSvg} alt="fb" />
                                        </div>
                                        <span>Crcoconet</span>
                                    </div>
                                </a>
                            </div>

                            <div className="footer__links__link">
                                <a href="https://www.instagram.com/croconet/" target="_blank" rel="noopener noreferrer">
                                    <div className="d-flex align-items-center">
                                        <div className="footer__links__link__icon">
                                            <img src={instSvg} alt="inst" />
                                        </div>
                                        <span>Crcoconet</span>
                                    </div>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="footer__bottom d-xl-block d-lg-block d-md-block d-none">
                    <div className="row">
                        <div className="col-4">
                            <img src={warning18} alt="18+" />
                        </div>
                        <div className="col-4 text-center">
                            <span className="footer__bottom__copyright">CROCONET.GE  © 2021</span>
                        </div>
                        <div className="col-4 d-flex justify-content-end">
                            <img src={topGe} alt="top.ge" />
                        </div>
                    </div>
                </div>

                {/* desktop version */}

                {/* mobile version */}

                <div className="d-xl-none d-lg-none d-md-none d-block">
                    <div className="d-flex flex-column justify-content-between footer__top__mobile">
                        <div className="footer__col">
                            <Link to="/home">
                                <div className="position-relative">
                                    <div className="footer__logo m-auto">
                                        <img src={footerLogo} alt="footer logo" />
                                    </div>
                                </div>
                            </Link>
                        </div>

                        <div className="footer__col">
                            <h4>კომპანია</h4>
                            <div className="footer__links">
                                <div className="footer__links__link">
                                    <Link>ფილმის მოთხოვნა</Link>
                                </div>

                                <div className="footer__links__link">
                                    <Link>რეკლამა საიტზე</Link>
                                </div>
                            </div>
                        </div>

                        <div className="footer__col">
                            <h4>კატეგორიები</h4>
                            <div className="footer__links">
                                <div className="footer__links__link">
                                    <Link to="/movies">ფილმები</Link>
                                </div>

                                <div className="footer__links__link">
                                    <Link to="/series">სერიალები</Link>
                                </div>

                                <div className="footer__links__link">
                                    <Link to="/my-list">ჩემი სიები</Link>
                                </div>

                                <div className="footer__links__link">
                                    <Link to="/news">სიახლეები</Link>
                                </div>
                            </div>
                        </div>

                        <div className="footer__col">
                            <h4>პროფილი</h4>
                            <div className="footer__links">
                                <div className="footer__links__link">
                                    <Link>ჩემი ანგარიში</Link>
                                </div>

                                <div className="footer__links__link">
                                    <Link>ჩემი გვერდი</Link>
                                </div>
                            </div>
                        </div>

                        <div className="footer__col">
                            <h4>დაგვიკავშირდით</h4>
                            <div className="footer__links d-flex">
                                <div className="footer__links__link social">
                                    <a href="tel:+995 123-123-123">
                                        <div className="d-flex align-items-center">
                                            <div className="footer__links__link__icon">
                                                <img src={phoneSvg} alt="phone" />
                                            </div>
                                        </div>
                                    </a>
                                </div>

                                <div className="footer__links__link social">
                                    <a href="mailto:Mail@mail.com">
                                        <div className="d-flex align-items-center">
                                            <div className="footer__links__link__icon">
                                                <img src={mailSvg} alt="mail" />
                                            </div>
                                        </div>
                                    </a>
                                </div>

                                <div className="footer__links__link social">
                                    <a href="https://www.facebook.com/Croconet.Ge/" target="_blank" rel="noopener noreferrer">
                                        <div className="d-flex align-items-center">
                                            <div className="footer__links__link__icon">
                                                <img src={fbSvg} alt="fb" />
                                            </div>
                                        </div>
                                    </a>
                                </div>

                                <div className="footer__links__link social">
                                    <a href="https://www.instagram.com/croconet/" target="_blank" rel="noopener noreferrer">
                                        <div className="d-flex align-items-center">
                                            <div className="footer__links__link__icon">
                                                <img src={instSvg} alt="inst" />
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="footer__bottom">
                        <div className="row footer__bottom__mobile__top">
                            <div className="col-6">
                                <img src={warning18} alt="18+" width="135" />
                            </div>

                            <div className="col-6 d-flex justify-content-end">
                                <img src={topGe} alt="top.ge" />
                            </div>
                        </div>
                    </div>
                    
                    <div className="w-100 text-center footer__credits__mobile">
                        <span className="footer__bottom__copyright">CROCONET.GE  © 2021</span>
                    </div>
                </div>

                {/* mobile version */}
            </div>
        </footer>
    )
}

export default Footer
