import React, { useCallback, useState } from 'react';
import Logo from "../../assets/images/croconet_logo.svg";
import Gradient from "../../assets/images/gradient.png";
import Search_logo from "../../assets/icons/search_logo.svg";
import { Link, NavLink, useLocation } from "react-router-dom";
import fetchLinks from "../../FetchLinks";
import useFetch from '../../custom-hooks/useFetch';
// import { useEffect,  } from 'react';
import { useDebounce } from 'use-debounce';

import { HamburgerSlider } from 'react-animated-burgers'
import MobileNav from './MobileNav';
import MobileCategories from './MobileCategories';
import MobileSearch from './MobileSearch';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';


export default function Nav() {
    const { data: moviesAndSeries, isPending, error } = useFetch(fetchLinks.moviesAndSeriesFetchLink);

    const [search, setSearch] = useState('');
    const [value] = useDebounce(search, 500);
    const [mobileMenuCategories, setMobileMenuCategories] = useState(false);
    const [mobileSearch, setMobileSearch] = useState(false)

    const updateSearch = e => {
        setSearch(e.target.value);
    }

    const [isButtonActive, setIsButtonActive] = useState(false)

    const toggleButton = useCallback(
        () => setIsButtonActive(prevState => !prevState),
        [],
    )
    const location = useLocation();

    const openCategories = () => {
        setMobileMenuCategories(true);
    }

    const closeCategories = () => {
        setMobileMenuCategories(false);
    }

    const openSearch = () => {
        setMobileSearch(true);
    }

    const closeSearch = () => {
        setMobileSearch(false);
    }

    if (mobileMenuCategories || mobileSearch) {
        document.body.classList.add('modal__preview')
    } else {
        document.body.classList.remove('modal__preview')
    }

    window.addEventListener('resize', function () {
        if (window.innerWidth >= 1250) {
            setMobileMenuCategories(false);
            setMobileSearch(false);
        }
    })

    return (
        <div className="nav">
            {/* mobile categories menu */}

            {
                mobileMenuCategories
                &&
                <div className="mobile__nav__categories">
                    <MobileCategories closeCategories={closeCategories} />
                </div>
            }

            {/* mobile search */}

            {
                mobileSearch
                &&
                <div className="mobile__search">
                    <MobileSearch closeSearch={closeSearch} />
                </div>
            }

            {/* mobile search */}

            <div class={`nav__mobile__top w-100  ${isButtonActive ? 'pb-0' : ''}`}>
                <div class="d-flex w-100">
                    <div>
                        <HamburgerSlider
                            barColor="white"
                            className="p-0 d-flex align-items-center outline-none"
                            buttonWidth={30}
                            {...{ isActive: isButtonActive, toggleButton }}
                        />
                    </div>
                    <NavLink exact to="/home" className="m-auto">
                        <img className="Navbar__logo" src={Logo} alt="croconet_logo" />
                    </NavLink>
                </div>

                <div className={`nav__mobile__menu ${isButtonActive ? '' : 'd-none'}`}>
                    <Swiper className=""
                        slidesPerView={5}
                        allowTouchMove={true}
                    >
                        <SwiperSlide className="nav__mobile__menu__link">
                            <NavLink exact to="/home" className="" activeClassName="active">მთავარი</NavLink>
                        </SwiperSlide>
                        <SwiperSlide className="nav__mobile__menu__link">
                            <NavLink to="/movies" className="" activeClassName="active">ფილმები</NavLink>
                        </SwiperSlide>
                        <SwiperSlide className="nav__mobile__menu__link">
                            <NavLink to="/series" className="" activeClassName="active">სერიალები</NavLink>
                        </SwiperSlide>
                        <SwiperSlide className="nav__mobile__menu__link">
                            <NavLink to="/my-list" className="" activeClassName="active">ჩემი სია</NavLink>
                        </SwiperSlide>
                        <SwiperSlide className="nav__mobile__menu__link">
                            <NavLink to="/news" className="" activeClassName="active">სიახლეები</NavLink>
                        </SwiperSlide>
                    </Swiper>
                </div>
            </div>
            <img src={Gradient} className="top__gradient" alt="" />

            <div className="Navbar">
                <div className="align-items-center Navbar__left">
                    <NavLink exact to="/home"><img className="Navbar__logo" src={Logo} alt="" /></NavLink>
                    <ul className="Navbar_list">
                        <li>
                            <NavLink exact to="/home" className="Navbar__link" activeClassName="active">მთავარი</NavLink>
                        </li>
                        <li>
                            <NavLink to="/movies" className="Navbar__link" activeClassName="active">ფილმები</NavLink>
                        </li>
                        <li>
                            <NavLink to="/series" className="Navbar__link" activeClassName="active">სერიალები</NavLink>
                        </li>
                        <li>
                            <NavLink to="/my-list" className="Navbar__link" activeClassName="active">ჩემი სია</NavLink>
                        </li>
                        <li>
                            <NavLink to="/news" className="Navbar__link" activeClassName="active">სიახლეები</NavLink>
                        </li>
                    </ul>
                </div>

                <div className="justify-content-between align-items-center Navbar__right">
                    <form className="form-group m-0" action="">
                        <div className="search">
                            <span className="form-control-feedback"><img src={Search_logo} alt="" /></span>
                            <input type="text" className="form-control" placeholder="ძებნა" value={search} onChange={updateSearch} />

                        </div>
                        <div className="movie">
                            {
                                moviesAndSeries.filter((val) => {
                                    if (search == "") {
                                        return null;
                                    } else if (val.title.toLowerCase().includes(value.toLowerCase())) {
                                        return val;
                                    }
                                    else if (val.geoTitle.toLowerCase().includes(value.toLowerCase())) {
                                        return val;
                                    }
                                }).map((val, key) => {
                                    return (
                                        <div className="movie__item d-flex  align-items-center justify-content-between" key={key}>
                                            <div className="d-flex align-items-center">
                                                <div style={{
                                                    backgroundImage: `url(${val.imgSrc})`,
                                                    width: '130px',
                                                    height: '70px',
                                                    backgroundPosition: "center",
                                                    backgroundRepeat: "no-repeat",
                                                    backgroundSize: "cover",
                                                    margin: " 15px 12px 10px 15px"
                                                }}></div>

                                                <div className="movie__item__title d-flex flex-column align-items-start">
                                                    <p className="movie__item__title--geotitle">{val.geoTitle}</p>
                                                    <p className="movie__item__title--entitle">{val.title}</p>
                                                    <p className="movie__item__title--year">{val.year}</p>
                                                </div>
                                            </div>

                                            <div>
                                                <a href=""><div className={`d-flex align-items-center justify-content-around movie__item__button movie__item__button--${val.type.toLowerCase()}`}>{val.Getype}</div> </a>
                                            </div>
                                        </div>
                                    );
                                })
                            }
                        </div>
                    </form>

                    <Link to={{
                        pathname: location.pathname,
                        search: `auth=true`
                    }}
                        onClick={() => document.querySelector('body').classList.add('modal__preview')}
                    >
                        <div className="Navbar__login">
                            <span>შესვლა</span>
                        </div>
                    </Link>
                </div>

                {/* mobile nav */}

                <div className="w-100 mobile__nav__wrapper">
                    <MobileNav openCategories={openCategories} openSearch={openSearch} />
                </div>
            </div>
        </div>
    )
}
