import React from 'react'
import useFetch from '../../custom-hooks/useFetch';
import { useEffect, useState } from 'react';
import Add from '../../assets/images/ad-1.jpg'
import fetchLinks from "../../FetchLinks"
const Trailers = () => {
    const { data: trailers, isPending, error } = useFetch(fetchLinks.trailerFetchLink)
    var i,j,temparray,chunk = 16;
    var chunks = [];
    for (i=0,j=trailers.length; i<j; i+=chunk) {
        temparray = trailers.slice(i,i+chunk);
        chunks.push(temparray);
    }
    const numOfArrays = chunks.length;
    return (
        <div>
            <h2 className="trailers-title">თრეილერები</h2>
            {
                chunks.map((el, index) => {
                    var items = (
                        <div className="trailers" >
                              {
                                   el.map((item, index) => {
                                       return <div  key={index} className="col p-0" >
                                       <a href="">
                                           <div className="trailers__item" style={{height: '320px', overflow: 'hidden'}} >
                                               <div alt={item.title} style = {{
                                                   backgroundImage: `url(${item.imgSrc})`,
                                                   width: '100%',
                                                   height: '100%',
                                                   backgroundPosition: "center",
                                                   backgroundRepeat: "no-repeat",
                                                   backgroundSize: "cover",
                                                   webkitBorderRadius: '5px',
                                                   mozBorderRadius: '5px',
                                               }} />
                                           </div>
                                       </a>
                                   </div>
                                   })
                              }
                        </div>
                           
                       );
                       if (numOfArrays - 1 !== index) {
                           var banner = <img className="add" src={Add} alt=""/>;
                       }
   
                       return (
                           [
                               items, 
                               banner
                           ]
                       )
                       
                })
            }
        </div>
    )
}

export default Trailers
