import React from 'react';
import useFetch from '../../custom-hooks/useFetch';
import Add from '../../assets/images/ad-1.jpg'
import fetchLinks from "../../FetchLinks"

const Collections = ({ }) => {
    const { data: collections, isPending, error } = useFetch(fetchLinks.collectionsFetchLink)
    var i,j,temparray,chunk = 18;
    var chunks = [];
    for (i=0,j=collections.length; i<j; i+=chunk) {
        temparray = collections.slice(i,i+chunk);
        chunks.push(temparray);
    }
    const numOfArrays = chunks.length;

    return ( 
        <div >
            <h2 className="collections_title">კოლექციები</h2>
            {
                chunks.map((el, index) => {
                    //console.log(el);
                    var items = (
                     <div className="collections row" >
                           {
                                el.map((item, index) => {
                                    return <div key={index} className="col-xl-2 col-lg-2 p-0" style={{height: '160px', overflow: 'hidden'}}>
                                    <a href="" className="d-block h-100">
                                        <div className="collections__item w-100">
                                            <div className="collections__item__title">
                                                <h3 className="collections__title">{item.title} </h3>
                                                <p>{item.quantity} ფილმი</p>
                                            </div>
                                            <div style = {{
                                                backgroundImage: `url(${item.imgSrc})`,
                                                width: '100%',
                                                height: '100%',
                                                backgroundPosition: "center",
                                                backgroundRepeat: "no-repeat",
                                                backgroundSize: "cover",
                                                webkitBorderRadius: '5px',
                                                mozBorderRadius: '5px',
                                            }}  />
                                        </div>
                                    </a>
                                </div>
                                })
                           }
                     </div>
                        
                    );
                    if (numOfArrays - 1 !== index) {
                        var banner = <img className="add" src={Add} alt=""/>;
                    }

                    return (
                        [
                            items, 
                            banner
                        ]
                    )
                    
                })
            }
        </div>
    )
}

export default Collections
