import { React, useEffect, useState } from 'react';

import fetchLinks from "../../FetchLinks"
import useFetch from '../../custom-hooks/useFetch';
import { useDebounce } from 'use-debounce';

import Search_logo from "../../assets/icons/search_logo.svg";
import clearSearchSvg from "../../assets/icons/clear_mobile_search.svg";

function MobileSearch(props) {
    const { data: moviesAndSeries, isPending, error } = useFetch(fetchLinks.moviesAndSeriesFetchLink);
    const [search, setSearch] = useState('');
    const [noResults, setNoResults] = useState(null);
    const [value] = useDebounce(search, 500);

    const updateSearch = e => {
        setSearch(e.target.value);
        if (document.querySelectorAll('.movie__item').length > 0) {
            setNoResults(true);
        } else {
            setNoResults(false);
        }
    }

    const clearSearch = () => {
        setSearch('');
    }

    useEffect(() => {
        document.querySelector('.mobile__search__input').focus();
    }, []);




    return (
        <div>
            <form className="form-group m-0" action="">
                <div className="search search--mobile">
                    <span className="form-control-feedback"><img src={Search_logo} alt="" /></span>
                    <input type="text" className="form-control mobile__search__input" placeholder="ძებნა" value={search} onChange={updateSearch} />
                    <div className={`close__search ${search === '' ? 'd-block' : 'd-none'}`} onClick={props.closeSearch}>გაუექმება</div>

                    <div className={`clear__search ${search === '' ? 'd-none' : 'd-block'}`} onClick={clearSearch}>
                        <img src={clearSearchSvg} alt="clear_search" />
                    </div>
                </div>
                <div className="movie movie--mobile">
                    {
                        moviesAndSeries.filter((val) => {
                            if (search == "") {
                                return null;
                            } else if (val.title.toLowerCase().includes(value.toLowerCase())) {
                                return val;
                            }
                            else if (val.geoTitle.toLowerCase().includes(value.toLowerCase())) {
                                return val;
                            }
                        }).map((val, key) => {
                            return (
                                <div className="movie__item movie__item--mobile d-flex  align-items-center justify-content-between" key={key}>
                                    <div className="d-flex align-items-center w-100">
                                        <a href="#" className="d-flex w-100">
                                            <div style={{
                                                backgroundImage: `url(${val.imgSrc})`,
                                                width: '100px',
                                                height: '63px',
                                                backgroundPosition: "center",
                                                backgroundRepeat: "no-repeat",
                                                backgroundSize: "cover",
                                                marginRight: " 15px",
                                                borderRadius: "5px"
                                            }}></div>

                                            <div className="movie__item__title d-flex flex-column align-items-start">
                                                <p className="movie__item__subtitle movie__item__title--geotitle">{val.geoTitle}</p>
                                                <p className="movie__item__subtitle movie__item__title--entitle">{val.title.toUpperCase()}</p>
                                                {/* <p className="movie__item__title--year">{val.year}</p> */}
                                                {val.Getype}
                                            </div>
                                        </a>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div>
                <div>
                    {
                        noResults && <div className="text-center" style={{ marginTop: '20px' }}>შედეგები არ არის...</div>
                    }
                </div>
            </form>
        </div>
    )
}

export default MobileSearch;
