import React, { useState } from 'react';

import homeSvg from '../../assets/icons/home.svg';
import categoriesSvg from '../../assets/icons/categories.svg';
import searchSvg from '../../assets/icons/search.svg';
import profileSvg from '../../assets/icons/profile.svg';

import { Link, useLocation } from 'react-router-dom';

function MobileNav(props) {
    
    const { pathname } = useLocation();
    return (
        <div>
            <div className="d-flex justify-content-center w-100 mobile__nav">
                <div class="mobile__nav__item">
                    <Link
                        onClick={(e) => {
                            if (pathname.split('/')[1] === 'home') {
                                e.preventDefault();
                                window.scroll({ top: 0, left: 0, behavior: 'smooth' })
                            }
                        }}
                        to="/"
                    >
                        <div className="d-flex flex-column align-items-center justify-content-center">
                            <div class="mobile__nav__icon">
                                <img src={homeSvg} alt="nav_icon" />
                            </div>
                            <span>მთავარი</span>
                        </div>
                    </Link>
                </div>

                <div class="mobile__nav__item" onClick={() => {
                    props.openCategories();
                }}>
                    <div class="mobile__nav__icon">
                        <img src={categoriesSvg} alt="nav_icon" />
                    </div>
                    <span>კატეგორია</span>
                </div>

                <div class="mobile__nav__item" onClick={() => {
                    props.openSearch();
                }}>
                    <div class="mobile__nav__icon">
                        <img src={searchSvg} alt="nav_icon" />
                    </div>
                    <span>ძიება</span>
                </div>

                <div class="mobile__nav__item">
                    <div class="mobile__nav__icon">
                        <img src={profileSvg} alt="nav_icon" />
                    </div>
                    <span>პროფილი</span>
                </div>
            </div>
        </div>
    )
}

export default MobileNav;
