import React, { Component } from 'react';
import { createPortal } from 'react-dom';

import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from 'yup';

import closeBtn from '../../../src/assets/icons/close.svg';
import { Link } from 'react-router-dom';
import ExternalLogin from './ExternalLogin';

const validationSchema = Yup.object().shape({
    userName: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    email: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    password: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    repeatPassword: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required')
});

class Registration extends Component {

    state = {
        registrationVals: {}
    }

    render() {
        const params = new URLSearchParams(this.props.location.search);

        const closeModal = e => {
            let modal = document.querySelector('.auth__modal');
            let closeBtn = document.querySelector('.auth__modal__close');

            if (e.target === modal || e.target === closeBtn) {
                setTimeout(() => {
                    this.props.history.push(this.props.location.pathname);
                }, 350);

                document.querySelector('.auth__modal__content').classList.add('slide__up');
                modal.classList.add('fade__out');
                document.querySelector('body').classList.remove('modal__preview');
            }
        }

        const disableAnimation = () => {
            let switchLink = document.querySelectorAll('.auth__modal__switcher__link');
            switchLink.forEach(link => {
                link.addEventListener('click', function () {
                    document.querySelector('.auth__modal').classList.remove('.slide__down');
                })
            })
        }

        const postValues = () => {

            fetch(`http://localhost:8000/users`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(this.state.registrationVals)
            })
              
        }

        return createPortal(
            params.get('register') &&

            <div className="auth__modal d-block" onClick={closeModal}>
                <div className="auth__modal__content">
                    <div
                        className="auth__modal__close d-flex justify-content-center align-items-center"
                        onClick={closeModal}>
                        <img src={closeBtn} alt="close_btn" />
                    </div>

                    <div className="auth__modal__switcher d-flex align-items-center text-center">
                        <div className="row w-100">
                            <div className="col-6 p-0">
                                <Link to={{
                                    pathname: this.props.location.pathname,
                                    search: `auth=true`
                                }}
                                    className="auth__modal__switcher__link"
                                    onClick={disableAnimation}
                                >ავტორიზაცია</Link>
                            </div>

                            <div className="col-6 p-0">
                                <Link to={{
                                    pathname: this.props.location.pathname,
                                    search: `register=true`
                                }}
                                    className="auth__modal__switcher__link active"
                                    onClick={disableAnimation}
                                >რეგისტრაცია</Link>
                            </div>
                        </div>
                    </div>

                    <div className="auth__modal__form">
                        <Formik
                            initialValues={{
                                userName: '',
                                email: '',
                                password: '',
                                repeatPassword: ''
                            }}
                            validationSchema={validationSchema}
                            onSubmit={values => {
                                console.log(values);
                                this.setState({ registrationVals: values });
                                postValues();
                            }}
                        >
                            {({ errors, touched, isSubmitting, isValid }) => (
                                <Form>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="text"
                                            name="userName"
                                            className="auth__modal__field__input"
                                            required
                                            autoComplete="off"
                                        />
                                        <label className="auth__modal__field__label">მომხმარებლის სახელი</label>

                                        {errors.firstName && touched.firstName ? (
                                            <div>{errors.firstName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="text"
                                            name="email"
                                            className="auth__modal__field__input"
                                            required
                                            autoComplete="off"
                                        />
                                        <label className="auth__modal__field__label">ელექტრონული ფოსტა</label>

                                        {errors.firstName && touched.firstName ? (
                                            <div>{errors.firstName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="password"
                                            name="password"
                                            className="auth__modal__field__input"
                                            required
                                        />
                                        <label className="auth__modal__field__label">პაროლი</label>

                                        {errors.password && touched.password ? (
                                            <div>{errors.lastName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="password"
                                            name="repeatPassword"
                                            className="auth__modal__field__input"
                                            required
                                        />
                                        <label className="auth__modal__field__label">გაიმეორეთ პაროლი</label>

                                        {errors.password && touched.password ? (
                                            <div>{errors.lastName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__submit auth__modal__submit--reg__submit d-flex justify-content-end">
                                        <button disabled={!isValid} type="submit">რეგისტრაცია</button>
                                    </div>
                                </Form>
                            )}
                        </Formik>
                    </div>

                    <div className="auth__modal__divider position-relative">
                        <div className="d-flex align-items-center justify-content-center">
                            <span>ან</span>
                        </div>
                    </div>

                    <ExternalLogin />
                </div>
            </div>,
            document.body
        )
    }
}

export default Registration
