import React from 'react'
import useFetch from '../../custom-hooks/useFetch';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay } from 'swiper';
import playIcon from '../../assets/icons/play_icon.svg';
import infoIcon from '../../assets/icons/info_icon.svg';



// Import Swiper styles
import 'swiper/swiper.min.css';

export default function Carousel({ fetchUrl }) {
    SwiperCore.use([Autoplay])
    const { data: slides, isPending, error } = useFetch(fetchUrl)
    return (
        <div className="headerCarousel">
            <Swiper
                loop={true}
                autoplay={{
                    delay: 4000,
                    disableOnInteraction: false

                }}
                speed={2000}
                slidesPerView={1}
            >
                {
                    slides?.map((el) => {
                        return <SwiperSlide
                            autoplay={false}
                            style={{
                                backgroundImage: `url(${el.imgSrc})`,
                            }}
                            className="headerCarousel__slide d-flex flex-column justify-content-end"
                            key={el.id}>
                            <div className="headerCarousel__titles">
                                <div className="headerCarousel__geotitle">{el.geoTitle}</div>
                                <div className="headerCarousel__entitle">{el.enTitle}</div>
                            </div>

                            <div className="headerCarousel__buttons ">
                                <a href=""><div className="headerCarousel__play d-flex align-items-center" > <img src={playIcon} alt="" /> <span>ჩართვა</span> </div></a>
                                <a href=""><div className="headerCarousel__seeMore d-flex align-items-center"><img src={infoIcon} alt="" /> <span>გაიგე მეტი</span> </div></a>
                            </div>
                        </SwiperSlide>
                    })
                }
            </Swiper>
        </div>
    )
}
