import React from 'react';

import play_icon from '../../assets/icons/play_icon.svg';
import plus_icon from '../../assets/icons/plus.svg';
import eye_icon from '../../assets/icons/eye.svg';
import dots_icon from '../../assets/icons/dots.svg';

import { Link, useLocation } from 'react-router-dom';

function OptGroup(props) {

    const location = useLocation();

    return (
        <div className="home__banner__optgroup w-100">
            <div className="home__banner__optgroup__header d-flex align-items-center justify-content-between">
                <div className="d-flex align-items-center">
                    <Link to={{
                        pathname: location.pathname,
                        search: `preview=true&category=${props.elTitle}&id=${props.elID}`
                    }}>
                        <div className="home__banner__optgroup__icon home__banner__optgroup__icon__1">
                            <img src={play_icon} alt="play_icon" />
                        </div>
                    </Link>

                    <div className="home__banner__optgroup__icon home__banner__optgroup__icon__2">
                        <img src={plus_icon} alt="plus_icon" />
                    </div>

                    <div className="home__banner__optgroup__icon home__banner__optgroup__icon__3">
                        <img src={eye_icon} alt="eye_icon" />
                    </div>
                </div>

                <Link
                    to={{
                        pathname: location.pathname,
                        search: `preview=true&category=${props.elTitle}&id=${props.elID}`
                    }}
                >
                    <div className="home__banner__optgroup__icon home__banner__optgroup__icon__4 home__banner__optgroup__icon--see__more">
                        <img src={dots_icon} alt="see_more" />
                    </div>
                </Link>
            </div>

            <div className="home__banner__optgroup__headings">
                <div className="home__banner__optgroup__heading home__banner__optgroup__heading__en">
                    <span>
                        {props.engTitle}
                    </span>
                </div>
                <div className="home__banner__optgroup__heading home__banner__optgroup__heading__ge">
                    <span>
                        {props.geoTitle}
                    </span>
                </div>
            </div>

            <div className="d-flex align-items-center">
                <div className="home__banner__optgroup__percentage">
                    <span>
                        {props.likePercentage}% მოსწონს
                        </span>
                </div>
                <div className="home__banner__optgroup__duration">
                    <span>
                        {props.duration}
                    </span>
                </div>
            </div>
        </div>
    )
}

export default OptGroup
