import React, { useEffect, useState } from 'react';
import { Formik, Form, Field } from "formik";
import Accordion from 'react-bootstrap/Accordion'

import filterNames from './FiltermenuCategories';

import filterArrow from '../../../src/assets/icons/filter-arrow.svg';
import filterIcon from '../../../src/assets/icons/filter_mobile.svg';
import closeSvg from '../../assets/icons/close.svg';
import { Card } from 'react-bootstrap';

function FilterHeaderMoviesMobile({ data, getFilteredValues, uncheckValue }) {

    const [openFiltersMenu, setOpenFiltersMenu] = useState(false);

    useEffect(() => {
        const byYear = document.getElementById('byYear');
        document.getElementById('byYear__button').addEventListener('mouseover', () => {
            byYear.classList.add('active');
        })

        document.addEventListener('click', (e) => {
            if (e.target.closest('#byYear__container') === null) {
                byYear.classList.remove('active')
            }
        })
    }, [data]);

    if (openFiltersMenu) {
        document.body.classList.add('modal__preview')
    } else {
        document.body.classList.remove('modal__preview')
    }

    const addAnimationDelay = () => {
        const elems = document.getElementsByClassName('detailed__filter__menu__container');

        for (let i = 0; i < elems.length; i++) {
            elems[i].style.animationDelay = i / 10 - 0.5 + 's';
        }
    }

    useEffect(() => {
        addAnimationDelay();
    });

    return (
        <div>
            <div class="d-flex align-items-center justify-content-between mobile__filter__group">
                <div className="open__filter__menu" onClick={() => setOpenFiltersMenu(true)}>
                    <img src={filterIcon} alt="filter" />
                    ფილტრაცია
                </div>
                <div className="dropdown__filter">გამოშვების წელი</div>
            </div>
            {
                openFiltersMenu
                &&

                <div className="mobile__filter">
                    <Formik
                        initialValues={{
                            genre: [],
                            dub: [],
                            yearFrom: '',
                            yearTo: '',
                            country: [],
                            IMDBrating: '',
                            ourRating: ''
                        }}

                        onSubmit={values => {
                            console.log(values);
                            getFilteredValues([values]);
                        }}
                    >
                        {({ values, handleChange, submitForm }) => (
                            (
                                <Form className="w-100 d-flex flex-wrap align-items-center">
                                    <Accordion className="w-100">
                                        {/* ჟანრი */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="0">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between">
                                                        <span>ჟანრი</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="0">
                                                <div className="detailed__filter__header__menu--mobile">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            {
                                                                filterNames.genres.map((el, ind) => {
                                                                    return <label key={ind}>
                                                                        <Field type="checkbox" name="genre" value={el} onChange={(e) => {
                                                                            handleChange(e);
                                                                            submitForm();
                                                                        }} />
                                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                                    </label>
                                                                })
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* ჟანრი */}

                                        {/* გახმოვანება */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="1">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between">
                                                        <span>გახმოვანება</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="1">
                                                <div className="detailed__filter__header__menu--mobile">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            {
                                                                filterNames.dub.map((el, ind) => {
                                                                    return <label key={ind}>
                                                                        <Field type="checkbox" name="dub" value={el} onChange={(e) => {
                                                                            handleChange(e);
                                                                            submitForm();
                                                                        }} />
                                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                                    </label>
                                                                })
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* გახმოვანება */}

                                        {/* წელი */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="2">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between" id="byYear__button">
                                                        <span>წელი</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="2">
                                                <div className="detailed__filter__header__menu--mobile" id="byYear">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            <label>
                                                                <input type="number" name="yearFrom" className="years__input" min="0" autoComplete="off" value={values.yearFrom} onChange={(e) => {
                                                                    handleChange(e);
                                                                    submitForm();
                                                                }} />
                                                                <span className="detailed__filter__name" title="დან">დან</span>
                                                            </label>

                                                            <label>
                                                                <input type="number" name="yearTo" className="years__input" min="0" autoComplete="off" value={values.yearTo} onChange={(e) => {
                                                                    handleChange(e);
                                                                    submitForm();
                                                                }} />
                                                                <span className="detailed__filter__name" title="მდე">მდე</span>
                                                            </label>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* წელი */}

                                        {/* ქვეყანა */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="3">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between">
                                                        <span>ქვეყანა</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="3">
                                                <div className="detailed__filter__header__menu--mobile">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            {
                                                                filterNames.countries.map((el, ind) => {
                                                                    return <label key={ind}>
                                                                        <Field type="checkbox" name="country" value={el} onChange={(e) => {
                                                                            handleChange(e);
                                                                            submitForm();
                                                                        }} />
                                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                                    </label>
                                                                })
                                                            }
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* ქვეყანა */}

                                        {/* IMDB რეიტინგი */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="4">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between">
                                                        <span>IMDB რეიტინგი</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="4">
                                                <div className="detailed__filter__header__menu--mobile">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            {
                                                                filterNames.IMDBratings.map((el, ind) => {
                                                                    return <label key={ind}>
                                                                        <Field type="radio" name="IMDBrating" value={el} onChange={(e) => {
                                                                            handleChange(e);
                                                                            submitForm();
                                                                        }} />
                                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                                    </label>
                                                                })
                                                            }

                                                            <Field type="radio" style={{ display: 'none' }} name="IMDBrating" value="" id="disable_IMDBrating" onChange={(e) => {
                                                                handleChange(e);
                                                                submitForm();
                                                            }} />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* IMDB რეიტინგი */}

                                        {/* ჩვენი რეიტინგი */}
                                        <div className="detailed__filter__menu__container">
                                            <Accordion.Toggle as={'div'} eventKey="5">
                                                <div className="detailed__filter__header__nav__elem">
                                                    <div className="d-flex align-items-center w-100 justify-content-between">
                                                        <span>ჩვენი რეიტინგი</span>
                                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                                            <img src={filterArrow} alt="filter_arrow" />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Toggle>

                                            <Accordion.Collapse eventKey="5">
                                                <div className="detailed__filter__header__menu--mobile">
                                                    <div className="detailed__filter__header__menu__scroll__container">
                                                        <div className="d-flex flex-column">
                                                            {
                                                                filterNames.IMDBratings.map((el, ind) => {
                                                                    return <label key={ind}>
                                                                        <Field type="radio" name="ourRating" value={el} onChange={(e) => {
                                                                            handleChange(e);
                                                                            submitForm();
                                                                        }} />
                                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                                    </label>
                                                                })
                                                            }

                                                            <Field type="radio" style={{ display: 'none' }} name="ourRating" value="" id="disable_ourRating" onChange={(e) => {
                                                                handleChange(e);
                                                                submitForm();
                                                            }} />
                                                        </div>
                                                    </div>
                                                </div>
                                            </Accordion.Collapse>
                                        </div>
                                        {/* ჩვენი რეიტინგი */}

                                        <div className="detailed__filter__menu__submit" onClick={() => { submitForm() }}>გაფილტვრა</div>
                                    </Accordion>
                                </Form>
                            )
                        )}
                    </Formik>

                    <div className="detailed__filter__menu__close" onClick={() => setOpenFiltersMenu(false)}>
                        <img src={closeSvg} alt="close_nav" />
                    </div>
                </div>
            }
        </div>
    )
}

export default FilterHeaderMoviesMobile;
