import React from 'react'
import fetchLinks from "../../FetchLinks"
import useFetch from '../../custom-hooks/useFetch';
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay } from 'swiper';
import editIcon from "../../assets/icons/edit-icon.svg"
import Add from '../../assets/images/ad-1.jpg'
function MyList() {
    const { data: history, isPending: historyisPending, error: historyError } = useFetch(fetchLinks.historyFetchLink);
    const { data: goingToWatch, isPending: goingToWatchisPending, error: goingToWatchError } = useFetch(fetchLinks.goingToWatchFetchLink);
    const { data: playlist, isPending: playlistisPending, error: playlistError } = useFetch(fetchLinks.playlistFetchLink);
    return (
        <div className="myList">
            <h3 className="myList__title">ჩემი სია</h3>
            <div className="myList__history">
                <h5 className="myList__history__title">ისტორია</h5>
                <Swiper
                    slidesPerView={6.3}
                    style={{ paddingLeft: '60px' }}
                    spaceBetween={5}
                >
                    {
                        history.map((el) => {
                            return <SwiperSlide key={el.id}>
                                <div className="myList__history__item" >
                                    <a href="
                                    ">
                                        <div style={{
                                            backgroundImage: `url(${el.imgSrc})`,
                                            width: '290px',
                                            height: '162px',
                                            backgroundPosition: "center",
                                            backgroundRepeat: "no-repeat",
                                            backgroundSize: "cover",
                                            webkitBorderRadius: '5px',
                                            mozBorderRadius: '5px'
                                        }} />
                                    </a>
                                    <div className="myList__history__item__progressbar">
                                        <div className="myList__history__item__inprogress" style={{ width: `${el.alreadyWatched / el.fullDuration * 100}%` }}></div>
                                    </div>
                                </div>
                            </SwiperSlide>
                        })
                    }
                </Swiper>
            </div>
            <div className="myList__goingToWatch">
                <div className="d-flex align-items-center" >
                    <h5 className="myList__goingToWatch__title">ვაპირებ ყურებას</h5>
                    <img src={editIcon} alt="" />
                </div>
                <Swiper
                    slidesPerView={6.3}
                    style={{ paddingLeft: '60px' }}
                >
                    {
                        goingToWatch.map((el) => {
                            console.log(el.imgSrc);
                            return <SwiperSlide>
                                <div className="myList__goingToWatch__item">
                                    <a href="">
                                        <div style={{
                                            backgroundImage: `url(${el.imgSrc})`,
                                            width: '290px',
                                            height: '162px',
                                            backgroundPosition: "center",
                                            backgroundRepeat: "no-repeat",
                                            backgroundSize: "cover",
                                            webkitBorderRadius: '5px',
                                            mozBorderRadius: '5px',
                                            marginRight: '5px'
                                        }} />
                                    </a>
                                </div>
                            </SwiperSlide>
                        })
                    }

                </Swiper>
            </div>
            <div className='banner'>
                <img src={Add} alt="banner" />
            </div>
            <div className="myList__playlist">
                <h5 className="myList__playlist__title">ფლეილისთი</h5>
            </div>
            <Swiper>

            </Swiper>
        </div>
    )
}

export default MyList
