import { createPortal } from 'react-dom';
import { useHistory } from 'react-router';

import closeSvg from '../../assets/icons/close_thin.svg';
import imdbSvg from '../../assets/icons/imdb_small.svg';
import eyeSvg from '../../assets/icons/eye.svg';
import plusSvg from '../../assets/icons/plus.svg';
import playSvg from '../../assets/icons/play_icon.svg';
import likeSvg from '../../assets/icons/like.svg';
import dislikeSvg from '../../assets/icons/dislike.svg';
import linkSvg from '../../assets/icons/link.svg';
import muteSvg from '../../assets/icons/mute.svg';
import downloadSvg from '../../assets/icons/download.svg';
import translateSvg from '../../assets/icons/translate.svg';
import exclamationSvg from '../../assets/icons/exclamation_mark.svg';

import adBanner from '../../assets/images/ad_movie_inside.png';

import { useEffect, useState } from 'react';

import useFetch from '../../custom-hooks/useFetch';
import { Link } from 'react-router-dom';
import SimilarMovies from './SimilarMovies';
import Comments from './Comments';
import { date } from 'yup';


function Modal() {

    const history = useHistory();
    const data = history.location.state;

    const params = new URLSearchParams(history.location.search);

    const [urlCategory, setUrlCategory] = useState('');
    const [urlId, setUrlId] = useState(null);
    const [movieToshow, setMovieToshow] = useState({});

    const closeModal = e => {
        let modal = document.querySelector('.preview');
        if (e.target === modal) {
            history.push(history.location.pathname);
            document.querySelector('body').classList.remove('modal__preview');
        }
    }

    const { data: movie, isPending, error } = useFetch(`http://localhost:8000/${urlCategory}`);


    const postComment = (input) => {
        let commentText = input.innerHTML;
        document.querySelector('.comments__field__input').innerText = '';
        let category = input.closest('.fieldgroup').getAttribute('data-category');

        function getDate() {
            let date = new Date().toLocaleDateString();
            let day = date.split('/')[1];
            let month = date.split('/')[0];
            let year = new Date().getFullYear();

            let fullDate, monthGeo;

            switch (month) {
                case '1':
                    monthGeo = 'იანვარი';
                    break;
                case '2':
                    monthGeo = 'თებერვალი';
                    break;
                case '3':
                    monthGeo = 'მარტი';
                    break;
                case '4':
                    monthGeo = 'აპრილი';
                    break;
                case '5':
                    monthGeo = 'მაისი';
                    break;
                case '6':
                    monthGeo = 'ივნისი';
                    break;
                case '7':
                    monthGeo = 'ივლისი';
                    break;
                case '8':
                    monthGeo = 'აგვისტო';
                    break;
                case '9':
                    monthGeo = 'სექტემბერი';
                    break;
                case 10:
                    monthGeo = 'ოქტომბერი';
                    break;
                case 11:
                    monthGeo = 'ნოემბერი';
                    break;
                case 12:
                    monthGeo = 'დეკემბერი';
                    break;
            }

            fullDate = day + ' ' + monthGeo + ' ' + year;
            return fullDate;
        }

        let data = {}


        if (category == "reply") {
            
             data = {
            "id": Math.floor(Math.random() * 10000000),
            "author": "user",
            "dateCreated": getDate(),
            "comment": "test comment",
            "likeCount": 0,
            "profilePicSrc": "https://dragonball.guru/wp-content/uploads/2021/01/goku-dragon-ball-guru.jpg",
            "replies": [
            ]
            }
            let reply = {
                "id": Math.floor(Math.random() * 10000000),
                "author": "user",
                "dateCreated": getDate(),
                "reply": commentText,
                "likeCount": 0,
                "profilePicSrc": "https://dragonball.guru/wp-content/uploads/2021/01/goku-dragon-ball-guru.jpg"
            }
            data.replies.push(reply);
        } else {
            data = {
                "id": Math.floor(Math.random() * 10000000),
                "author": "user",
                "dateCreated": getDate(),
                "comment": commentText,
                "likeCount": 0,
                "profilePicSrc": "https://dragonball.guru/wp-content/uploads/2021/01/goku-dragon-ball-guru.jpg",
                "replies": [
                ]
            }
        }
        console.log(category);
        let thisMovie = movieToshow;
        thisMovie.comments.push(data);
        setMovieToshow(thisMovie);

        fetch(`http://localhost:8000/${urlCategory}/${urlId}/`, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(thisMovie)
        })
    }

    useEffect(() => {
        if (params.get('preview')) {
            let category = params.get('category').split('=')[0];
            let id = params.get('id').split('=')[0];
            setUrlCategory(category);
            setUrlId(id);

            const movToShow = movie.find(el => el.id === parseInt(urlId));
            setMovieToshow(movToShow);
            document.body.classList.add('modal__preview')

            // console.log(comm);
        }
    })

    return createPortal(

        params.get('preview') &&

        <div className="preview d-block" onClick={closeModal}>
            {error && <div>{error}</div>}

            {
                isPending ? <div>Loading...</div> : <div className="preview__page" id="preview">
                    <div className="preview__page__inside">

                        <div
                            className="preview__page__close"
                            onClick={() => {
                                history.push(history.location.pathname);
                                document.querySelector('body').classList.remove('modal__preview');
                            }}>
                            <img src={closeSvg} alt="close" />
                        </div>

                        <div className="preview__page__banner d-flex flex-column justify-content-end" style={{ backgroundImage: `url(https://miro.medium.com/max/800/1*5RvnrSJSr9GLlEetdQB34Q.png)` }}>

                            <div className="preview__page__banner__play">
                                <Link to="/movies">
                                    <div className="preview__page__banner__play__button">
                                        <img src={playSvg} alt="play" />
                                    </div>
                                </Link>
                            </div>

                            <div className="preview__page__banner__info">
                                <div className="preview__page__banner__info__heading--ge">
                                    <h2>
                                        {isPending && <div>Loading...</div>}
                                        {movieToshow?.geoTitle}
                                    </h2>
                                </div>

                                <div className="preview__page__banner__info__heading--en">
                                    <h2>
                                        {isPending && <div>Loading...</div>}
                                        {movieToshow?.engTitle}
                                    </h2>
                                </div>

                                <div className="d-flex align-items-center">
                                    <div className="preview__page__banner__info__percentage">
                                        <span>
                                            {isPending && <div>Loading...</div>}
                                            {movieToshow?.likePercentage} % მოსწონს
                                        </span>
                                    </div>

                                    <div className="preview__page__banner__info__imdb d-flex align-items-center">
                                        <img src={imdbSvg} alt="imdb" />
                                        <span>
                                            {isPending && <div>Loading...</div>}
                                            {movieToshow?.imdb}
                                        </span>
                                    </div>

                                    <div className="preview__page__banner__info__views d-flex align-items-center">
                                        <img src={eyeSvg} alt="imdb" />
                                        <span> ნანახია 5412</span>
                                    </div>
                                </div>
                            </div>

                            <div className="preview__page__banner__buttons d-flex justify-content-between">
                                <div className="d-flex align-items-center">
                                    <Link className="text-decoration-none">
                                        <div className="preview__page__banner__trailer__button">
                                            <img src={playSvg} alt="trailer" />
                                            <span>თრეილერი</span>
                                        </div>
                                    </Link>

                                    <div className="preview__page__banner__icon">
                                        <img src={plusSvg} alt="plus" />
                                    </div>

                                    <div className="preview__page__banner__icon">
                                        <img src={eyeSvg} alt="plus" />
                                    </div>

                                    <div className="preview__page__banner__icon">
                                        <img src={likeSvg} alt="plus" />
                                    </div>

                                    <div className="preview__page__banner__icon">
                                        <img src={dislikeSvg} alt="plus" />
                                    </div>

                                    <div className="preview__page__banner__icon">
                                        <img src={linkSvg} alt="plus" />
                                    </div>
                                </div>

                                <div className="preview__page__banner__icon preview__page__banner__icon--mute">
                                    <img src={muteSvg} alt="mute" />
                                </div>
                            </div>
                        </div>

                        <div className="preview__page__info">
                            <div className="row">
                                <div className="col-8">
                                    <div className="col-11">
                                        <span className="preview__page__info__description">
                                            {movieToshow?.description}
                                        </span>
                                    </div>
                                </div>
                                <div className="col-4 d-flex flex-column info__group">
                                    <span>
                                        <span className="preview__page__info__small__heading">ჟანრი: </span>
                                        {movieToshow?.genres?.map((e, i) => {
                                            return (i === 0 ? ' ' : ',') + e
                                        })}
                                    </span>

                                    <span>
                                        <span className="preview__page__info__small__heading">წელი: </span>
                                        {movieToshow?.year}
                                    </span>

                                    <span>
                                        <span className="preview__page__info__small__heading">რეჟისორი: </span>
                                        {movieToshow?.director}
                                    </span>

                                    <span>
                                        <span className="preview__page__info__small__heading">მსახიობები: </span>
                                        {movieToshow?.actors?.map((e, i) => {
                                            return (i === 0 ? ' ' : ',') + e
                                        })}
                                    </span>

                                    <div className="preview__page__info__buttons">
                                        <div className="preview__page__info__button preview__page__info__button--report__problem">
                                            <img src={exclamationSvg} alt="report" />
                                            <span>პრობლემის შეტყობინება</span>
                                        </div>

                                        <div className="preview__page__info__button preview__page__info__button--want__geo">
                                            <img src={translateSvg} alt="georgian" />
                                            <span>მინდა ქართულად</span>
                                        </div>

                                        <div className="preview__page__info__button preview__page__info__button--download">
                                            <img src={downloadSvg} alt="download" />
                                            <span>გადმოწერა</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="preview__page__ad__banner w-100">
                            <img src={adBanner} alt="ad" />
                        </div>

                        <SimilarMovies />
                        <Comments commentsList={movieToshow?.comments} postComment={postComment} />
                    </div>
                </div>
            }
        </div>,
        document.body
    )
}

export default Modal
