import React from 'react';
import Row from './Row';

import ad1 from '../../assets/images/ad-1.jpg';

import fetchLinks from '../../FetchLinks';
import TwoRow from './TwoRow';
import Carousel from '../Header/Carousel';

function Home(props) {
    return (
        <div className="home">
            <Carousel fetchUrl={fetchLinks.headerCarouselFetchLink} />
            <Row
                links={[
                    {
                        linkTitle: 'ფილმები',
                        linkUrl: '/movies',
                        category: 'movie'
                    },
                    {
                        linkTitle: 'სერიალები',
                        linkUrl: '/series',
                        category: 'series'
                    }
                ]}
                className="georgian"
                title="ქართულად გახმოვანებული"
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/"
            />

            <Row className="continue__watching" title="გააგრძელე ყურება" fetchUrl={fetchLinks.georgianDubFetchLink} forModalProps={props.forModalProps} />

            <div className="container-fluid p-0 home__ad__block">
                <img src={ad1} alt="" className="w-100" />
            </div>

            <TwoRow
                links={[
                    {
                        linkTitle: 'დღის',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: 'თვის',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: 'წლის',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="newly__added__series"
                title="ახალი დამატებული ეპიზოდები"
                fetchUrl={fetchLinks.newlyAddedSeriesFetchLink}
            />

            <Row
                className="newly__added__movies"
                title="ახალი დამატებული"
                fetchUrl={fetchLinks.newlyAddedMoviesFetchLink}
                seeMoreUrl="/"
            />

            <Row
                links={[
                    {
                        linkTitle: 'დღის',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: 'თვის',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: 'წლის',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="top__movies"
                title="ტოპ ფილმები"
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/"
            />

            <Row
                links={[
                    {
                        linkTitle: 'დღის',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: 'თვის',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: 'წლის',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="top__series"
                title="ტოპ სერიალები"
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/" 
                />
                
            <Row
                className="trailers"
                title="თრეილერები"
                fetchUrl={fetchLinks.trailerFetchLink}
                seeMoreUrl="/trailers" 
                />


            <TwoRow
                className="collections"
                title="კოლექციები"
                fetchUrl={fetchLinks.collectionsFetchLink}
                seeMoreUrl="/collections"
            />


        </div>
    )
}

export default Home
