import React, { useEffect, useState } from 'react';
import { Formik, Form, Field } from "formik";

import filterArrow from '../../../src/assets/icons/filter-arrow.svg';

import filterNames from './FiltermenuCategories';

function FilterHeaderMovies({ data, getFilteredValues, uncheckValue }) {
    const [changedValue, setChangedValue] = useState(0)

    useEffect(() => {
        const byYear = document.getElementById('byYear');
        document.getElementById('byYear__button').addEventListener('mouseover', () => {
            byYear.classList.add('active');
        })

        document.addEventListener('click', (e) => {
            if (e.target.closest('#byYear__container') === null) {
                byYear.classList.remove('active')
            }
        })
    }, [data])



    useEffect(() => {
        let inputs = document.querySelectorAll('input');
        inputs.forEach(input => {
            if (input.value === uncheckValue && (input.type === 'radio' || input.type === 'checkbox')) {
                if (input.type === 'checkbox') {
                    input.click();
                }
                input.checked = false;

                if (input.type === 'radio') {
                    document.getElementById('disable_' + input.name).click();
                }
            }
        });
    }, [uncheckValue]);


    return (
        <div className="detailed__filter__header d-flex">
            <div style={{ backgroundColor: '#1A1A1A', height: '75px', paddingLeft: '120px', paddingRight: '70px' }} className="detailed__filter__header__nav__elem">
                <div className="d-flex">
                    <span>ფილტრაცია</span>
                </div>
            </div>

            <Formik
                initialValues={{
                    genre: [],
                    dub: [],
                    yearFrom: '',
                    yearTo: '',
                    country: [],
                    IMDBrating: '',
                    ourRating: ''
                }}

                onSubmit={values => {
                    getFilteredValues([values]);
                }}
            >
                {({ values, handleChange, submitForm }) => (
                    (
                        <Form className="w-100 d-flex align-items-center">
                            {/* ჟანრი */}
                            <div className="detailed__filter__menu__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center">
                                        <span>ჟანრი</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            {
                                                filterNames.genres.map((el, ind) => {
                                                    return <label key={ind}>
                                                        <Field type="checkbox" name="genre" value={el} onChange={(e) => {
                                                            handleChange(e);
                                                            submitForm();
                                                        }} />
                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                    </label>
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* ჟანრი */}

                            {/* გახმოვანება */}
                            <div className="detailed__filter__menu__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center">
                                        <span>გახმოვანება</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            {
                                                filterNames.dub.map((el, ind) => {
                                                    return <label key={ind}>
                                                        <Field type="checkbox" name="dub" value={el} onChange={(e) => {
                                                            handleChange(e);
                                                            submitForm();
                                                        }} />
                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                    </label>
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* გახმოვანება */}

                            {/* წელი */}
                            <div className="detailed__filter__menu__container" id="byYear__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center" id="byYear__button">
                                        <span>წელი</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu" id="byYear">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            <label>
                                                <input type="number" name="yearFrom" className="years__input" min="0" autoComplete="off" value={values.yearFrom} onChange={(e) => {
                                                    handleChange(e);
                                                    submitForm();
                                                }} />
                                                <span className="detailed__filter__name" title="დან">დან</span>
                                            </label>

                                            <label>
                                                <input type="number" name="yearTo" className="years__input" min="0" autoComplete="off" value={values.yearTo} onChange={(e) => {
                                                    handleChange(e);
                                                    submitForm();
                                                }} />
                                                <span className="detailed__filter__name" title="მდე">მდე</span>
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* წელი */}

                            {/* ქვეყანა */}
                            <div className="detailed__filter__menu__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center">
                                        <span>ქვეყანა</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            {
                                                filterNames.countries.map((el, ind) => {
                                                    return <label key={ind}>
                                                        <Field type="checkbox" name="country" value={el} onChange={(e) => {
                                                            handleChange(e);
                                                            submitForm();
                                                        }} />
                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                    </label>
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* ქვეყანა */}

                            {/* IMDB რეიტინგი */}
                            <div className="detailed__filter__menu__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center">
                                        <span>IMDB რეიტინგი</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            {
                                                filterNames.IMDBratings.map((el, ind) => {
                                                    return <label key={ind}>
                                                        <Field type="radio" name="IMDBrating" value={el} onChange={(e) => {
                                                            handleChange(e);
                                                            submitForm();
                                                        }} />
                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                    </label>
                                                })
                                            }

                                            <Field type="radio" style={{ display: 'none' }} name="IMDBrating" value="" id="disable_IMDBrating" onChange={(e) => {
                                                handleChange(e);
                                                submitForm();
                                            }} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* IMDB რეიტინგი */}

                            {/* ჩვენი რეიტინგი */}
                            <div className="detailed__filter__menu__container">
                                <div className="detailed__filter__header__nav__elem">
                                    <div className="d-flex align-items-center">
                                        <span>ჩვენი რეიტინგი</span>
                                        <div className="detailed__filter__header__nav__elem__arrow d-flex align-items-center">
                                            <img src={filterArrow} alt="filter_arrow" />
                                        </div>
                                    </div>
                                </div>

                                <div className="detailed__filter__header__menu">
                                    <div className="detailed__filter__header__menu__scroll__container">
                                        <div className="d-flex flex-column">
                                            {
                                                filterNames.IMDBratings.map((el, ind) => {
                                                    return <label key={ind}>
                                                        <Field type="radio" name="ourRating" value={el} onChange={(e) => {
                                                            handleChange(e);
                                                            submitForm();
                                                        }} />
                                                        <span className="detailed__filter__name" title={el}>{el}</span>
                                                    </label>
                                                })
                                            }

                                            <Field type="radio" style={{ display: 'none' }} name="ourRating" value="" id="disable_ourRating" onChange={(e) => {
                                                handleChange(e);
                                                submitForm();
                                            }} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* ჩვენი რეიტინგი */}
                        </Form>
                    )
                )}
            </Formik>
        </div>
    )
}

export default FilterHeaderMovies;