import React from 'react';
import useFetch from '../../custom-hooks/useFetch';
import fetchLinks from '../../FetchLinks';

import { Swiper, SwiperSlide } from 'swiper/react';
import OptGroup from './OptGroup';
import { Link, useLocation } from 'react-router-dom';



function SimilarMovies() {
    const { data: similarMovies, isPending, error } = useFetch(fetchLinks.similarMoviesFetchLink);
    const location = useLocation();

    const scrollToTop = () => {
        const scrollTarget = document.getElementById('preview');
        scrollTarget.scrollTop = 0;
    }

    return (
        <div className="similar__movies">
            {error && <div>{error}</div>}
            {isPending && <div>Loading...</div>}

            <div className="similar__movies__heading">
                <h2>მსგავსი ფილმები</h2>
            </div>

            <Swiper
                slidesPerView={5.3}
                centeredSlides={true}
                centeredSlidesBounds={true}
                initialSlide={2}
                slidesPerGroup={5}
                spaceBetween={5}
                allowTouchMove={false}
                loop={true}
                speed={1700}
                navigation
            >
                {
                    similarMovies?.map((el, ind) => {
                        return <SwiperSlide
                            key={ind}
                            className={`similar__movies__banner`}
                        >
                            <Link to={{
                                pathname: location.pathname,
                                search: `preview=true&category=${el.urlCategory}&id=${el.id}`
                            }}
                                className="w-100"
                                onClick={scrollToTop}
                            >
                                <img src={el.imgSrc} alt={el.title} className="main-border-radius w-100" />
                            </Link>

                            <OptGroup
                                likePercentage={el.likePercentage || ''}
                                duration={el.duration || ''}
                                geoTitle={el.geoTitle || ''}
                                engTitle={el.engTitle || ''}
                                isGeorgian={el.isGeorgian || ''}
                                isEnglish={el.isEnglish || ''}
                                isRussian={el.isRussian || ''}
                                elTitle={el.urlCategory}
                                elID={el.id}
                                scrollToTop={scrollToTop}
                            />
                        </SwiperSlide>
                    })
                }
            </Swiper>
        </div>
    )
}

export default SimilarMovies
