import React, { Component } from 'react';
import { createPortal } from 'react-dom';

import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from 'yup';

import closeBtn from '../../../src/assets/icons/close.svg';
import { Link } from 'react-router-dom';
import ExternalLogin from './ExternalLogin';

const validationSchema = Yup.object().shape({
    userName: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required'),
    password: Yup.string()
        .min(2, 'Too Short!')
        .max(50, 'Too Long!')
        .required('Required')
});

class Authorization extends Component {
    render() {
        const params = new URLSearchParams(this.props.location.search);

        const closeModal = e => {
            let modal = document.querySelector('.auth__modal');
            let closeBtn = document.querySelector('.auth__modal__close');

            if (e.target === modal || e.target === closeBtn) {
                setTimeout(() => {
                    this.props.history.push(this.props.location.pathname);
                }, 350);

                document.querySelector('.auth__modal__content').classList.add('slide__up');
                modal.classList.add('fade__out');
                document.querySelector('body').classList.remove('modal__preview');
            }
        }

        // console.log(this.props.history);

        return createPortal(
            params.get('auth') &&

            <div className="auth__modal d-block" onClick={closeModal}>
                <div className="auth__modal__content">
                    <div
                        className="auth__modal__close d-flex justify-content-center align-items-center"
                        onClick={closeModal}>
                        <img src={closeBtn} alt="close_btn" />
                    </div>

                    <div className="auth__modal__switcher d-flex align-items-center text-center">
                        <div className="row w-100">
                            <div className="col-6 p-0">
                                <Link to={{
                                    pathname: this.props.location.pathname,
                                    search: `auth=true`
                                }}
                                    className="auth__modal__switcher__link active">ავტორიზაცია</Link>
                            </div>

                            <div className="col-6 p-0">
                                <Link to={{
                                    pathname: this.props.location.pathname,
                                    search: `register=true`
                                }}
                                    className="auth__modal__switcher__link">რეგისტრაცია</Link>
                            </div>
                        </div>
                    </div>

                    <div className="auth__modal__form">
                        <Formik
                            initialValues={{
                                userName: '',
                                password: ''
                            }}
                            validationSchema={validationSchema}
                            onSubmit={values => {
                                // same shape as initial values
                                console.log(values);
                            }}
                        >
                            {({ errors, touched, isSubmitting, isValid }) => (
                                <Form>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="text"
                                            name="userName"
                                            className="auth__modal__field__input"
                                            required
                                            autoComplete="off"
                                        />
                                        <label className="auth__modal__field__label">მომხმარებლის სახელი</label>

                                        {errors.firstName && touched.firstName ? (
                                            <div>{errors.firstName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__field">
                                        <Field
                                            type="password"
                                            name="password"
                                            className="auth__modal__field__input"
                                            required
                                        />
                                        <label className="auth__modal__field__label">პაროლი</label>

                                        {errors.password && touched.password ? (
                                            <div>{errors.lastName}</div>
                                        ) : null}
                                    </div>

                                    <div className="auth__modal__forgot__pass">
                                        <Link to={{
                                              pathname: this.props.location.pathname,
                                              search: `forgotPass=true`
                                        }}>დაგავიწყდა პაროლი ?</Link>
                                    </div>

                                    <div className="auth__modal__submit d-flex justify-content-end">
                                        <button disabled={!isValid} type="submit">შესვლა</button>
                                    </div>
                                </Form>
                            )}
                        </Formik>
                    </div>

                    <div className="auth__modal__divider position-relative">
                        <div className="d-flex align-items-center justify-content-center">
                            <span>ან</span>
                        </div>
                    </div>

                    <ExternalLogin />
                </div>
            </div>,
            document.body
        )
    }
}

export default Authorization
