import React, { useEffect } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { Redirect, Route, Switch, useLocation } from "react-router";

// Main css file that holds all styles
import './styles/Main.css';

// Components
import Header from "./components/Header/Header";
import Home from './components/Home/Home';
import Movies from './components/Movies-and-Series/Movies';
import MyList from './components/My-list/MyList';
import News from './components/News/News';
import Modal from './components/Movie-inside-page/Modal';
import Collections from './components/Collections/Collections'
import Trailers from './components/Trailers/Trailers'
import Footer from './components/Footer/Footer';
import Series from './components/Movies-and-Series/Series';
import Authorization from './components/Authorization/Authorization';
import Registration from './components/Authorization/Registration';
import ForgotPass from './components/Authorization/ForgotPass';

function App() {

  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname])

  return (
    <div>
      <div className="App">
        <Header />

        <Switch>
          <Redirect exact from="/" to="/home" />
          <Route exact path="/home" component={Home} />
          <Route path="/movies" component={Movies} />
          <Route path="/series" component={Series} />
          <Route path="/my-list" component={MyList} />
          <Route path="/news" component={News} />
          <Route path="/collections" component={Collections} />
          <Route path="/trailers" component={Trailers}></Route>
        </Switch>

        {/* Portal routes */}
        <Route path="/" component={Modal} />
        <Route path="/" component={Authorization} />
        <Route path="/" component={Registration} />
        <Route path="/" component={ForgotPass} />
      </div>

      <Footer />
    </div>
  );
}


export default App;